/*Обнуление*/
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 800;margin:0.5em 0;}
/*--------------------*/
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
  box-sizing: border-box;
}

body{
  font-family: 'Rubik', sans-serif;
  margin: 0;
  font-size: 10px;
  --darkColor: black;
  --lightColor: white;
  background-color: var(--darkColor);
  color: var(--lightColor);
  --mainBgComponent: rgba(13, 13, 92, 0.21);
  --borderColor: rgba(255, 255, 255, 0.61);
  --bgHover: rgba(255, 255, 255, 0.148); 
}
body::-webkit-scrollbar {
    width: 6px;

}
body::-webkit-scrollbar-thumb{
    background: linear-gradient(rgb(194, 0, 194), rgb(0, 140, 255));
    border-radius: 10px;
}