.app-wrapper{ 
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: 64px 1fr;
  height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  gap: 16px;
  padding-bottom: 16px;
}
.app-wrapper > div{
  background-color: var(--mainBgComponent);
  border-radius: 10px;
  padding: 10px;
}
.messageItem_My {
  align-self: flex-end;
  background-color: rgba(27, 75, 152, 0.699);
}
.messageItem_Friends {
  background-color: var(--darkColor);
  align-self: flex-start;
}
.active {
  background-color: rgba(27, 75, 152, 0.699);
}
.active:hover {
  background-color: rgba(27, 75, 152, 0.863) !important;
}